html{
  height: 100%;
}
p, div{
  margin: 0;
}
img, a{
  outline: 0;
}
body{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: auto;
  -webkit-font-smoothing: antialiased;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 10px;
  -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
  -moz-tap-highlight-color:rgba(0, 0, 0, 0);
  -ms-tap-highlight-color:rgba(0, 0, 0, 0);
  -o-tap-highlight-color:rgba(0, 0, 0, 0);
  tap-highlight-color:rgba(0, 0, 0, 0);
}
p{
  line-height: 100%;
  vertical-align: baseline;
  font-size: "Microsoft YaHei"!important;
}
a{
  text-decoration: none;
}
*{
  font-family: Arial;
}
.pointer{
  cursor: pointer;
}
#app{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}
#navBar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
#fixed-nav{
  position: fixed;
  left: 0;
}
.grey, .grey2{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.6;
}
.grey2{
  opacity: 0;
}
.modal-enter-active{
  -webkit-animation: modal .2s;
  animation: modal .2s;
}
.modal-leave-active{
  -webkit-animation: modal .2s reverse;
  animation: modal .2s reverse;
}
@-webkit-keyframes modal {
0%{
    -webkit-transform: translate(-50%, -30px);
}
100%{
    -webkit-transform: translate(-50%, 0);
}
}
@keyframes modal {
0%{
    -webkit-transform: translate(-50%, -30px);
            transform: translate(-50%, -30px);
}
100%{
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}
}
.promptBox{
  height: auto;
  position: fixed;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  background: white;
}
